.header {
  scroll-behavior: smooth;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  transition: all .3s ease-in-out;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__link {
  width: 40px;
  height: 40px;
  margin: 10px 50px;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.header__nav {
  margin: 0 50px 0 0;
  display: none;
}

@media (width >= 768px) {
  .header__nav {
    display: flex;
  }
}

.header__menu {
  margin-inline: 50px;
  display: block;
}

@media (width >= 768px) {
  .header__menu {
    display: none;
  }
}

.header .nav {
  flex: row;
  display: none;
}

@media (width >= 768px) {
  .header .nav {
    display: flex;
  }
}

.header .nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header .nav .nav__item {
  padding: 0 10px;
}

.header .nav .nav__item:first-child {
  padding-left: 0;
}

.header .nav .nav__item:last-child {
  padding-right: 0;
}

.header .nav .nav__item .nav__link {
  text-align: center;
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.header .nav .nav__item .nav__link:visited {
  color: #000;
}

.header .nav .nav__item .nav__link:hover {
  color: #00acdc;
}

.header .nav .nav__item .nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.header .nav .nav__item .nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 4px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

